import { ForgotpwdPage } from './../forgotpwd/forgotpwd.page';
import { Component, OnInit } from '@angular/core';
import { ModalController, NavController } from '@ionic/angular';
import { RegisterPage } from '../register/register.page';
import { NgForm } from '@angular/forms';
import { AuthService } from 'src/app/services/auth.service';
import { AlertService } from 'src/app/services/alert.service';
import { FacebookLoginResponse } from '@ionic-native/facebook';
import { Facebook } from '@ionic-native/facebook/ngx';
import { NativeStorage } from '@ionic-native/native-storage/ngx';
import { GooglePlus } from '@ionic-native/google-plus/ngx';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  constructor(
    private fb: Facebook,
    private googlePlus: GooglePlus,
    private modalController: ModalController,
    private authService: AuthService,
    private navCtrl: NavController,
    private alertService: AlertService,
    private nativeStorage: NativeStorage
  ) { }
  ngOnInit() {
  }
  // Dismiss Login Modal
  dismissLogin() {
    this.modalController.dismiss();
  }
  // On Register button tap, dismiss login modal and open register modal
  async registerModal() {
    this.dismissLogin();
    const registerModal = await this.modalController.create({
      component: RegisterPage
    });
    return await registerModal.present();
  }
  // Dismiss Login Modal
  forgotPwd() {
    this.modalController.dismiss();
  }
  // On Register button tap, dismiss login modal and open register modal
  async forgotpwdModal() {
    this.forgotPwd();
    const forgotpwdModal = await this.modalController.create({
      component: ForgotpwdPage
    });
    return await forgotpwdModal.present();
  }
  login(form: NgForm) {
    this.authService.login(form.value.pinNumber).subscribe(
      data => {
        this.alertService.presentToast('Logged In');
      },
      error => {
        console.log(error);
      },
      () => {
        this.dismissLogin();
        this.navCtrl.navigateRoot('/dashboard');
      }
    );
  }
  loginFB() {
    const permissions = ['public_profile', 'email'];
    this.fb.login(permissions).then(response => {
      const userId = response.authResponse.userID;
      this.fb.api('/me?fields=name,email', permissions)
        .then(user => {
          user.picture = 'https://graph.facebook.com/' + userId + '/picture?type=large';
          this.nativeStorage.setItem('facebook_user',
            {
              name: user.name,
              email: user.email,
              picture: user.picture
            })
            .then(() => {
              this.navCtrl.navigateRoot(['/dashboard']);
              this.alertService.presentToast('Login FB success');
            }, error => {
              console.log(error);
            });
        });
    }, error => {
      console.log(error);
    });
  }


  loginGG() {
    this.googlePlus.login({
      'scopes':'https://www.googleapis.com/auth/plus.login',
      'webClientId':'com.googleusercontent.apps.907796142943-cl3jisj4amab9no3o8025r3gk3bcppgb',
      'offline': true
    }).then(user => {
      this.nativeStorage.setItem('google_user', {
        name: user.displayName,
        email: user.email,
        picture: user.imageUrl
      })
      .then(() =>{
        this.navCtrl.navigateRoot(['/dashboard']);
      }, error => {
        console.log(error);
      });
    }, err => {
      console.log(err);
    }).catch(err => console.log(err)
    );
  }
}
