import { Component, OnInit } from '@angular/core';
import { ModalController, NavController, NavParams } from '@ionic/angular';
import { LoginPage } from '../login/login.page';
import { AuthService } from 'src/app/services/auth.service';
import { AlertService } from 'src/app/services/alert.service';
import { HttpClient } from '@angular/common/http';
import { NativeStorage } from '@ionic-native/native-storage/ngx';
import { EnvService } from 'src/app/services/env.service';
import { RequestregisterPage } from 'src/app/pages/auth/requestregister/requestregister.page';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {
  arrayEmail = [];
  arrayPhone = [];
  email: string;
  phone: string;
  lengData: number;
  mydataemail: string;
  mydataphone: string;

  constructor(private modalController: ModalController,
              private authService: AuthService,
              private navCtrl: NavController,
              private alertService: AlertService,
              private http: HttpClient,
              private storage: NativeStorage,
              private env: EnvService,
              private router: Router,
              private navParams: NavParams) { }

  ngOnInit() {
  }

  dismissRegister() {
    this.modalController.dismiss();
  }

  isregister() {
    this.http.get(this.env.API_URL + 'users/isUser' + '?email=' + this.email + '&mobileNo=' + this.phone).subscribe(
      data => {
        this.lengData = Object.keys(data).length;
        for (let index = 0; index < this.lengData; index++) {
          if (data[index].email === this.email && data[index].mobileNo === this.phone) {
            this.dismissRegister();
            this.router.navigate(['requestregister']);
            this.mydataemail = this.email;
            localStorage.setItem('objectToPassEmail', this.mydataemail);
            this.mydataphone = this.phone;
            localStorage.setItem('objectToPassPhone', this.mydataphone);
          } else {
            this.dismissRegister();
            this.router.navigate(['requestregister']);
          }
        }
      }, err => {
        console.log(err);
      }
    );
  }
}
