import { AlertService } from './../../../services/alert.service';
import { AuthService } from './../../../services/auth.service';
import { NgForm } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { ModalController, NavController } from '@ionic/angular';
import { error } from '@angular/compiler/src/util';

@Component({
  selector: 'app-forgotpwd',
  templateUrl: './forgotpwd.page.html',
  styleUrls: ['./forgotpwd.page.scss'],
})
export class ForgotpwdPage implements OnInit {

  constructor(
    private modalController: ModalController,
    private authService: AuthService,
    private alertService: AlertService,
    private navCtrl: NavController
  ) { }

  ngOnInit() {
  }

  dismissResetPwd() {
    this.modalController.dismiss();
  }

  forgotpwd(form: NgForm) {
    this.authService.forgotpwd(form.value.email, form.value.mobileNo).subscribe(
      data => {
        this.alertService.presentToast('Reset password');
      },
      error => {
        console.error();
      }

    );
  }
}
